const BASE_API_URL = './test-data';
let API_URLS       = {
  LOGIN              : {
    READ: '/profile/dologin',
  },
  TRANSACTIONS       : {
    READ: '/transactions.json',
  },
  TRANSACTION_DETAILS: {
    READ: '/transaction-details.json',
  },
  ISSUES             : {
    READ: '/issues.json',
  },
  ISSUE_DETAILS      : {
    READ: '/issue-details.json',
  },
  REFUND_PAYMENTS    : {
    UPDATE: '/trx/transfer',
  },
  REPLY_ISSUES       : {
    UPDATE: '/issue/insertTrail',
  },
};

API_URLS = _.mapValues(API_URLS, resource =>
  _.mapValues(resource, apiUrl => BASE_API_URL + apiUrl)
);

export {
  DATA_TYPE,
  TABLE_TYPE,
  API_URLS
};



